import styles from './LoginForm.module.css'
import { Form, Field } from 'react-final-form'
import { FORM_ERROR } from 'final-form';
import {authAPI} from '../../../api/api'

const onSubmit = props => async values => {
    const data = {
        "field":values['field'],
        "password":values['password']
    }
    const response = await authAPI.login(data)
    if(response?.data?.resultCode == 0) {
        for(let key in values) {
            values[key] = undefined
        }
        props.app.getUser()
        return { [FORM_ERROR]: [<div className={styles.mobile_form_success}>  </div>,null]}
    } else if(response?.data?.resultCode == 2) {
        return {[FORM_ERROR]: [<div className={styles.mobile_form_error}>{response?.data?.message}</div>,true]}
    } else {
        return {[FORM_ERROR]: [<div className={styles.mobile_form_error}>{response?.data?.message}</div>,null]}
    }

}

const required = value => (value ? undefined : '*    ')
const minValue = min => value => (value.length >= min ? undefined : `*      ${min} `)
const maxValue = max => value => (value.length <= max ? undefined : `*      ${max} `)
const composeValidators = (...validators) => value =>
    validators.reduce((error, validator) => error || validator(value), undefined)

const LoginFormMobile = (props) => {
    return (
        <div className={styles.mobile_form}>
            <Form
                onSubmit={onSubmit(props)}
                render={({ submitError,handleSubmit, form, submitting, pristine, values }) => (
                    <form onSubmit={handleSubmit}>

                        <Field name="field" validate={composeValidators(required, minValue(5), maxValue(30))}>
                            {({ input, meta }) => (
                                <div>
                                    <div className={styles.mobile_title}>  email:</div>
                                    <input {...input} className={meta.error && meta.touched && styles.error || submitError && submitError[1] && styles.error} type="text" placeholder="   email" ></input>
                                    {meta.error && meta.touched && <div className={styles.error_message}>{meta.error}</div>}
                                </div>
                            )}
                        </Field>
                        <Field name="password" validate={composeValidators(required, minValue(5), maxValue(20))}>
                            {({ input, meta }) => (
                                <div>
                                    <div className={styles.mobile_title}>:</div>
                                    <input {...input} className={meta.error && meta.touched && styles.error} type="password" placeholder=" " ></input>
                                    {meta.error && meta.touched && <div className={styles.error_message}>{meta.error}</div>}
                                </div>
                            )}
                        </Field>
                        <button className={styles.mobile_button} type="submit" disabled={submitting}></button>
                        {submitError && submitError[0]}
                    </form>
                )}
            />


        </div>
    )
}

export default LoginFormMobile